.animation {
    display: none;
}

.nav_item {
    z-index: 999;
}
@media screen and (min-width: 1415px) and (max-width: 1540px) {


.nav_menu .animation {
    display: block;
  position: absolute;
  height: 100%;
  /* height: 5px; */
  top: 0;
  /* bottom: 0; */
  z-index: 0;
  background: #1abc9c;
  border-radius: 8px;
  transition: all .8s ease 0s;
}
.nav_menu li, .animation {
  width: 7.8vw;
}
.nav_menu .home,
.nav_menu li:nth-child(1):hover~.animation {
  left: 45%;
}


.nav_menu .about,
.nav_menu  li:nth-child(2):hover~.animation {
  left: 54%;
}


.nav_menu .projects,
.nav_menu li:nth-child(3):hover~.animation {
  left: 63%;
}

.nav_menu .contact,
.nav_menu li:nth-child(4):hover~.animation {
  left: 72%;
}

}

@media screen and (min-width: 1540px)  and (max-width: 1836px) {

.nav_menu li, .animation {
  width: 7.8vw;
}
.nav_menu .home,
.nav_menu li:nth-child(1):hover~.animation {
